body{
	background: #fff;
}
.titulo_padrao{
	font-family: roboto;
	font-weight: 600;
	color: $quaternary-color;
}
.titulo_padrao--primary{
	color: $primary-color;
}
.titulo_padrao--secondary{
	color: $secondary-color;
}
.titulo_padrao--tertiary{
	color: $tertiary-color;
}
.titulo_padrao--cta{
	color: $cta-color;
}
.titulo_padrao--uppercase{
	text-transform: uppercase;
}
.btn--cta{
	color: #fff!important;
	background-color: $cta-color!important;
	text-transform: capitalize;
	&:disabled{
		background-color: rgba($cta-color, 0.65)!important;	
	    display: flex;
	    align-items: center;
	    justify-content: center;
	    img{
	    	max-height: 100%;
	    	max-width: 2em;
	    }
	}
}
.text-center{
	text-align: center;
}
.btn--totalWidth{
	width: 100%;
}
.menu__icon{
	cursor: pointer;
	img{
		width: 65%;	
		padding-top: 10px;	
	}
}
.login__content__logo--menu{
	padding: 0px 30px;
}
.footer{
	background: $secondary-color;
	height: 15px;
    position: absolute;
    bottom: 0px;
    width: 100%;
}
ul{
	list-style-type: none; 
	padding: 0px;
	li{
		list-style: none!important;
	}
}
.back__cover{
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}
.back__contain{
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
}